.product-section {
  background-color: $title-color;
  padding-top: 121px;
  padding-bottom: 98px;
  @media screen and (min-width: 768px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.grid {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -20px;
  margin-right: -20px;
  margin-bottom: -17px;
  margin-top: 0;
}

.grid__item {
  max-width: 280px;
  margin-bottom: 17px;
  margin-left: 20px;
  margin-right: 20px;
  @media screen and (min-width: 768px) {
    max-width: 220px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 329px;
  }

  @media screen and (min-width: 768px) {
    flex-basis: calc(100% / 3 - 17px);
  }
}

.section__title {
  @media screen and (min-width: 768px) {
    margin-bottom: 39px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 49px;
  }
}

.product {
  &::before {
    content: '';
    display: inline-block;
    width: 100%;
    height: 252px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 249px 252px;
    vertical-align: middle;
    padding-left: 16px;
    padding-right: 15px;
    margin-bottom: -139px;

    @media screen and (min-width: 768px) {
      padding-left: 0;
      padding-right: 0;
      height: 226px;
      background-size: 220px 226px;
    }

    @media screen and (min-width: 1280px) {
      height: 335px;
      background-size: 335px 335px;
      margin-bottom: -130px;
    }
  }
}

.product--one::before {
  background-image: url(/images/products/pic1_m_1x-min.png);

  @media screen and (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(/images/products/pic1_m_2x-min.png);
  }

