.product__name {
  font-family: $accent-font;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 30px;
  position: relative;
  padding-top: 161px;
  @media screen and (min-width: 768px) {
    padding-top: 152px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 163px;
    margin-bottom: 32px;
    font-size: 30px;
    line-height: 1.13;
  }

  &::after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 26px;
    height: 6px;
    background-image: url(/images/icons/group4.svg);
    background-size: 26px 6px;
    bottom: -30px;
    left: 45%;
  }
  &--modal {
    padding-top: 0;
    padding-bottom: 12px;
    text-align: centernp;
  }
}

.product__description {
  font-weight: bold;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  margin: 66px 0 30px 0;
  padding: 0 39px 0 41px;
  max-width: 100%;
  height: 124px;
  overflow: hidden;
  @media screen and (min-width: 768px) {
    line-height: 1.8;
    margin: 66px 0 40px 0;
    padding: 0 11px 0 11px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.76;
    height: 106px;
    margin: 70px 0 62px 0;
    padding: 0 44px 0 44px;
  }
}

.product__about {
  text-align: center;
  border-radius: 24px;
  &--one {
    background-color: $background-hero-color;
  }

  &--two {
    background-color: #f0d1a5;
  }

  &--three {
    background-color: #c2e297;
  }
}

