.product__name {
  font-family: $accent-font;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 30px;
  position: relative;
  padding-top: 161px;

  &--modal {
    padding: 0;
  }
  @media screen and (min-width: 768px) {
    padding-top: 152px;
    &--modal {
      padding: 0;
    }
  }

  @media screen and (min-width: 1280px) {
    padding-top: 163px;
    margin-bottom: 32px;
    font-size: 30px;
    line-height: 1.13;
    &--modal {
      padding: 0;
    }
  }

  &::after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 26px;
    height: 6px;
    background-image: url(/images/icons/group4.svg);
    background-size: 26px 6px;
    bottom: -30px;
    left: 45%;
  }
}

.product__description {
  overflow: hidden;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  margin: 66px 0 30px 0;
  padding: 0 39px 0 41px;
  max-width: 100%;
  height: 124px;

  &--modal {
    padding: 15px;
    border: 1px solid white;
    border-radius: 24px;
    height: auto;
  }
  @media screen and (min-width: 768px) {
    line-height: 1.8;
    margin: 66px 0 40px 0;
    padding: 0 11px 0 11px;

    &--modal {
      padding: 15px;
      height: auto;
    }
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.76;
    height: 106px;
    margin: 70px 0 62px 0;
    padding: 0 44px 0 44px;
    &--modal {
      padding: 15px;
      height: auto;
    }
  }
}

.product__about {
  text-align: center;
  border-radius: 24px;
  &--one {
    background-color: $background-hero-color;
  }

  &--two {
    background-color: #f0d1a5;
  }

  &--three {
    background-color: #c2e297;
  }
}

.product__button {
  display: inline-block;
  position: relative;
  height: 40px;
  width: 40px;
  background-image: url(/images/icons/arrow-right_p.svg);
  margin-bottom: 41px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 250ms;

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 50px;
  }
  &:hover,
  &:focus {
    transform: scale(1.15);
  }
}

.modal__close {
  display: inline-block;
  height: 30px;
  width: 30px;
  background-image: url(/images/products/close.svg);
  background-size: 30px 30px;
  background-repeat: no-repeat;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 250ms;
  position: absolute;
  right: 10px;
  top: 10px;
  transition: transform 250ms;
  &:hover,
  &:focus {
    transform: scale(1.15);
  }
}

.modal-wrap:before {
  content: '';
  display: none;
  background: rgba(209, 164, 164, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 101;
}
.modal-overlay {
  bottom: 0;
  display: none;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 102;
}

.modal-open:checked ~ .modal-wrap:before,
.modal-open:checked ~ .modal-wrap .modal-overlay {
  display: block;
}
.modal-open:checked ~ .modal-wrap .modal-dialog {
  transform: translate(-50%, 0);
  top: 5%;
}

.modal-dialog {
  border-radius: 24px;
  position: fixed;
  left: 50%;
  top: -100%;
  transform: translate(50%, -50%);
  transition: transform 0.4s ease-out;
  height: 550px;
  width: 320px;
  z-index: 103;
  padding: 40px;
  text-align: center;
  background: linear-gradient(0deg, rgb(225, 121, 146), rgb(255, 184, 202));

  &--two {
    background: linear-gradient(0deg, rgb(225, 121, 146), rgb(240, 209, 165));
  }

  &--three {
    background: linear-gradient(0deg, rgb(225, 121, 146), rgb(194, 226, 151));
  }
}
